          <div class="generation-limits" v-if="!isVipUser">
            <p> {{freeUserDailyLimit}}  <el-button type="text" @click="openVipOptions"></el-button></p>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="image-preview-panel">
        <div class="preview-header">
          <h3 class="preview-title"></h3>
          <div class="preview-actions" v-if="generatedImages.length > 0">
            <el-button size="small" type="primary" icon="el-icon-download" @click="saveAllImages"></el-button>
            <el-button size="small" icon="el-icon-delete" @click="clearResults"></el-button>
