            <div class="feature-item" v-for="(feature, i) in plan.features" :key="i">
              <i class="el-icon-check"></i>
              <span>{{ feature }}</span>
            </div>
          </div>
          <el-button 
            :type="vipDialog.selectedPlan && vipDialog.selectedPlan.id === plan.id ? 'primary' : 'default'"
            @click.stop="confirmVipPurchase(plan)"
          >{{ vipDialog.selectedPlan && vipDialog.selectedPlan.id === plan.id ? '' : '' }}</el-button>
        </div>
      </div>
