<template>
  <div class="tagger-tab">
    <div class="tab-header">
      <h2 class="tab-title">WD1.4 </h2>
      <p class="tab-description">AI</p>
    </div>
    
    <div class="tab-content">
      <!--  -->
      <div class="left-section">
        <div class="upload-section">
          <h3></h3>
          <image-uploader
            v-model="sourceImage"
            @change="handleImageChange"
          />
          <div class="image-tips" v-if="!sourceImage">
            <p></p>
            <p>PNGJPG</p>
          </div>
        </div>
      </div>
      
      <!--  -->
      <div class="right-section">
        <div class="tagger-settings">
          <h3></h3>
          
          <div class="setting-item">
            <label></label>
            <el-select v-model="taggerModel" placeholder="">
              <el-option label="WD 1.4 Tagger" value="wd14"></el-option>
              <el-option label="DeepDanbooru" value="deepdanbooru"></el-option>
              <el-option label="CLIP" value="clip"></el-option>
            </el-select>
          </div>
          
          <div class="setting-item">
            <label></label>
            <el-select v-model="tagLanguage" placeholder="">
              <el-option label="" value="zh"></el-option>
              <el-option label="" value="en"></el-option>
              <el-option label="" value="jp"></el-option>
            </el-select>
          </div>
          
          <div class="setting-item">
            <label></label>
            <div class="slider-with-value">
              <el-slider v-model="confidenceThreshold" :min="0" :max="1" :step="0.01"></el-slider>
              <span class="value-display">{{ confidenceThreshold.toFixed(2) }}</span>
            </div>
          </div>
          
          <div class="checkbox-options">
            <div class="checkbox-option">
              <el-checkbox v-model="includeCharacter"></el-checkbox>
            </div>
            <div class="checkbox-option">
              <el-checkbox v-model="includeGeneral"></el-checkbox>
            </div>
            <div class="checkbox-option">
              <el-checkbox v-model="excludeNSFW">NSFW</el-checkbox>
            </div>
            <div class="checkbox-option">
              <el-checkbox v-model="sortByConfidence"></el-checkbox>
            </div>
          </div>
        </div>
        
        <!--  -->
        <div class="generate-button-wrapper">
          <el-button 
            type="primary" 
            size="medium" 
            :loading="isProcessing" 
            :disabled="!canProcess"
            @click="extractTags">
            
          </el-button>
        </div>
      </div>
      
      <!--  -->
      <div v-if="hasTags" class="results-section">
        <div class="results-header">
          <h3></h3>
          <div class="results-actions">
            <el-button size="small" type="primary" @click="copyAllTags"></el-button>
            <el-button size="small" @click="clearResults"></el-button>
          </div>
        </div>
        
        <div class="tags-container">
          <!--  -->
          <div class="tags-list">
            <div v-for="(tag, index) in extractedTags" :key="index" class="tag-item">
              <span class="tag-name">{{ tag.name }}</span>
              <div class="tag-confidence-bar" :style="{ width: tag.confidence * 100 + '%' }"></div>
              <span class="tag-confidence">{{ (tag.confidence * 100).toFixed(1) }}%</span>
            </div>
          </div>
          
          <!--  -->
          <div class="formatted-prompt">
            <h4></h4>
            <el-input
              type="textarea"
              v-model="formattedPrompt"
              :rows="6"
              readonly
            ></el-input>
            <div class="prompt-actions">
              <el-button size="small" type="primary" @click="copyPrompt"></el-button>
              <el-button size="small" @click="useInTextToImage"></el-button>
            </div>
          </div>
        </div>
      </div>
      
    </div>
    
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="loadingDialog"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
      width="30%">
      <div class="loading-dialog-content">
        <el-progress :percentage="processingProgress" :format="progressFormat"></el-progress>
        <p>...</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ImageUploader from '@/components/ImageUploader.vue'
import { simulateImageGeneration } from '@/utils/aiUtils'

export default {
  name: 'TaggerTab',
  components: {
    ImageUploader
  },
  data() {
    return {
      // 
      sourceImage: null,
      
      // 
      taggerModel: 'wd14',
      tagLanguage: 'zh',
      confidenceThreshold: 0.35,
      includeCharacter: true,
      includeGeneral: true,
      excludeNSFW: true,
      sortByConfidence: true,
      
      // 
      isProcessing: false,
      loadingDialog: false,
      processingProgress: 0,
      
      // 
      extractedTags: [],
      formattedPrompt: ''
    }
  },
  computed: {
    canProcess() {
      return this.sourceImage !== null;
    },
    hasTags() {
      return this.extractedTags.length > 0;
    }
  },
  methods: {
    handleImageChange(file) {
      if (file) {
        // 
        this.extractedTags = [];
        this.formattedPrompt = '';
      }
    },
    progressFormat(percentage) {
      return percentage + '%';
    },
    async extractTags() {
      if (!this.canProcess) return;
      
      this.isProcessing = true;
      this.loadingDialog = true;
      this.processingProgress = 0;
      
      try {
        // 
        await simulateImageGeneration((progress) => {
          this.processingProgress = progress;
        }, 3000);
        
        // 
        this.generateMockTags();
        
        this.$message.success('');
      } catch (error) {
        this.$message.error('');
        console.error(':', error);
      } finally {
        this.isProcessing = false;
        this.loadingDialog = false;
      }
    },
    generateMockTags() {
      // 
      const mockTags = [
        { name: '', confidence: 0.99, type: 'character' },
        { name: '', confidence: 0.95, type: 'general' },
        { name: '', confidence: 0.93, type: 'general' },
        { name: '', confidence: 0.89, type: 'general' },
        { name: '', confidence: 0.85, type: 'general' },
        { name: '', confidence: 0.82, type: 'general' },
        { name: '', confidence: 0.78, type: 'general' },
        { name: '', confidence: 0.76, type: 'general' },
        { name: '', confidence: 0.68, type: 'general' },
        { name: '', confidence: 0.65, type: 'general' },
        { name: '', confidence: 0.62, type: 'general' },
        { name: '', confidence: 0.59, type: 'general' },
        { name: '', confidence: 0.55, type: 'general' },
        { name: '', confidence: 0.48, type: 'general' },
        { name: '', confidence: 0.42, type: 'general' },
        { name: '', confidence: 0.39, type: 'general' },
        { name: '', confidence: 0.36, type: 'general' }
      ];
      
      // 
      this.extractedTags = mockTags.filter(tag => {
        if (tag.confidence < this.confidenceThreshold) return false;
        if (!this.includeCharacter && tag.type === 'character') return false;
        if (!this.includeGeneral && tag.type === 'general') return false;
        return true;
      });
      
      // 
      if (this.sortByConfidence) {
        this.extractedTags.sort((a, b) => b.confidence - a.confidence);
      }
      
      // 
      this.formattedPrompt = this.extractedTags
        .map(tag => tag.name + (this.tagLanguage === 'en' ? ' ' : ''))
        .join('');
    },
    copyAllTags() {
      const tagsText = this.extractedTags.map(tag => tag.name).join('');
      navigator.clipboard.writeText(tagsText).then(() => {
        this.$message.success('');
      }).catch(() => {
        this.$message.error('');
      });
    },
    copyPrompt() {
      navigator.clipboard.writeText(this.formattedPrompt).then(() => {
        this.$message.success('');
      }).catch(() => {
        this.$message.error('');
      });
    },
    useInTextToImage() {
      this.$message.success('');
      // 
      this.$emit('use-in-text2img', this.formattedPrompt);
    },
    clearResults() {
      this.extractedTags = [];
      this.formattedPrompt = '';
    }
  }
}
</script>

<style scoped>
.tagger-tab {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.tab-header {
  margin-bottom: 10px;
}

.tab-title {
  font-size: 20px;
  font-weight: 500;
  color: #303133;
  margin: 0 0 8px;
}

.tab-description {
  font-size: 14px;
  color: #606266;
  margin: 0;
}

.tab-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
}

.left-section, .right-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.upload-section, .tagger-settings {
  background-color: #f5f7fa;
  border-radius: 8px;
  padding: 20px;
}

h3 {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
  margin-top: 0;
  margin-bottom: 15px;
}

h4 {
  font-size: 14px;
  font-weight: 500;
  color: #303133;
  margin: 15px 0 10px;
}

.image-tips {
  margin-top: 15px;
  font-size: 13px;
  color: #909399;
}

.image-tips p {
  margin: 5px 0;
}

.setting-item {
  margin-bottom: 20px;
}

.setting-item label {
  display: block;
  margin-bottom: 8px;
  font-size: 14px;
  color: #606266;
}

.slider-with-value {
  display: flex;
  align-items: center;
}

.slider-with-value .el-slider {
  flex: 1;
  margin-right: 15px;
}

.value-display {
  width: 40px;
  text-align: right;
  font-size: 14px;
  color: #606266;
}

.checkbox-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.checkbox-option {
  margin-bottom: 5px;
}

.generate-button-wrapper {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.results-section {
  grid-column: 1 / -1;
  margin-top: 10px;
}

.results-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.results-header h3 {
  margin: 0;
  font-size: 18px;
  color: #303133;
}

.tags-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
  background-color: #f5f7fa;
  border-radius: 8px;
  padding: 20px;
}

.tags-list {
  max-height: 400px;
  overflow-y: auto;
  padding-right: 10px;
}

.tag-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  position: relative;
  padding: 8px 10px;
  border-radius: 4px;
  background-color: white;
}

.tag-name {
  flex: 1;
  z-index: 1;
}

.tag-confidence-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: rgba(103, 194, 58, 0.1);
  border-radius: 4px;
}

.tag-confidence {
  width: 60px;
  text-align: right;
  font-size: 12px;
  color: #67c23a;
  z-index: 1;
}

.formatted-prompt {
  display: flex;
  flex-direction: column;
}

.prompt-actions {
  margin-top: 15px;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.loading-dialog-content {
  padding: 20px 0;
  text-align: center;
}

.loading-dialog-content p {
  margin-top: 15px;
  color: #606266;
}

@media screen and (max-width: 992px) {
  .tab-content, .tags-container {
    grid-template-columns: 1fr;
  }
}
</style> 